<template>
  <div class="seller" id="se">
    <div class="seller-content">
      <div class="overview">
        <h1 class="title">{{seller.name}}</h1>
        <div class="desc border-1px">
          <star :size="36" :scoe="seller.score"></star>
          <span class="text">{{seller.ratingCount}}</span>
          <span class="text">月售{{seller.sellCount}}单</span>
        </div>
        <ul class="remark">
          <li class="block">
            <h2>起送价</h2>
            <div class="content">
              <span class="stress">{{seller.minPrice}}</span>元

            </div>
          </li>
          <li class="block">
            <h2>商家配送</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryPrice}}</span>元

            </div>
          </li>
          <li class="block">
            <h2>平均配送时间</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryTime}}</span>分钟

            </div>
          </li>
        </ul>
        <div class="favorite" @click="toggleFavorite">
          <span class="icon-favorite" :class="{active:favorite}"></span>
          <span class="text">{{favoriteText}}</span>
        </div>
      </div>
      <split></split>
      <div class="bulletin">
        <h1 class="title">公告与活动</h1>
        <div class="content-wrapper border-1px">
          <p class="content">{{seller.bulletin}}</p>
        </div>
        <ul v-if="seller.supports" class="supports">
          <li class="support-item border-1px" v-for="item in seller.supports">
            <span class="icon" :class="classMap[item.type]"></span>
            <span class="text">{{item.description}}</span>
          </li>
        </ul>
      </div>
      <split></split>
      <div class="pics">
        <h1 class="title">商家实景</h1>
        <div class="pic-wrapper" id="picList">
          <ul class="pic-list" id="pic">
            <li class="pic-item" v-for="pic in seller.pics">
              <img :src="pic" alt="" width="120" height="90">
            </li>
          </ul>
        </div>
      </div>
      <split></split>
      <div class="info">
        <h1 class="title border-1px">商家信息</h1>
        <ul>
          <li class="info-item" v-for="info in seller.infos">{{info}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
  import Star from 'components/star/star'
  import Split from 'components/split/Split'
  import BScroll from 'better-scroll'
  import {saveToLocal,loadFromLocal} from '../../common/js/store'
  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data(){
        return {
          favorite:(()=>{
              return loadFromLocal(this.seller.id,'favorite',false)
          })()
        }
    },
    computed:{
      favoriteText(){
          return this.favorite?'已收藏':'收藏'
      }
    },
    created(){
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'special']
    },
    watch: {
        seller(){
          this.$nextTick(()=>{this._initScroll();});
          this.$nextTick(()=>{this._initPics();})
        }
    },
    components: {Star, Split},
    mounted(){
      this.$nextTick(()=>{this._initScroll();});
      this.$nextTick(()=>{this._initPics();});
    },
    methods: {
      _initScroll(){
        if (!this.scroll) {
          this.scroll = new BScroll(document.getElementById('se'), {
            click: true
          })
        } else {
          this.scroll.refresh();
        }
      },
      _initPics(){
        if(this.seller.pics){
          let picWidth=120;
          let margin=6;
          let width=(picWidth+margin)*this.seller.pics.length-6;
          document.getElementById('pic').style.width=width+'px';
          this.$nextTick(()=>{
              if(!this.picScroll){
                this.picScroll=new BScroll(document.getElementById('picList'),{
                    scrollX:true,
                  eventPassthrough:'vertical'
                })
              }else{
                this.picScroll.refresh();
              }
          });
        }
      },
      toggleFavorite(){
          if(!event._constructed){
              return
          }
        this.favorite=!this.favorite;
        saveToLocal(this.seller.id,'favorite',this.favorite)
      }
    }
  }
</script>
<style scoped lang="less">
  @import "../../common/less/mixin";

  .seller {
    position: absolute;
    top: 174px;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    .overview {
      position: relative;
      padding: 18px;
      .title {
        margin: 8px 0;
        line-height: 14px;
        font-size: 14px;
        color: rgb(7, 17, 27);
      }
      .desc {
        padding-bottom: 18px;
        .border-1px(rgba(7, 17, 27, 0.1));
        font-size: 0;
        .star {
          display: inline-block;
          margin-right: 8px;
        }
        .text {
          display: inline-block;
          vertical-align: top;
          margin-right: 12px;
          font-size: 10px;
          line-height: 18px;
          color: rgb(77, 85, 93);
        }
      }
      .remark {
        display: flex;
        padding-top: 18px;
        .block {
          flex: 1;
          text-align: center;
          border-right: 1px solid rgba(7, 17, 27, 0.1);
          h2 {
            margin-bottom: 4px;
            line-height: 10px;
            font-size: 10px;
          }
          .content {
            line-height: 24px;
            font-size: 10px;
            font-weight: 200;
            color: rgb(77, 85, 93);
            .stress {
              font-size: 24px;
            }
          }
          &:last-child {
            border-right: none;
          }
        }
      }
      .favorite{
        position: absolute;
        width: 50px;
        right: 11px;
        top:18px;
        text-align: center;
        .icon-favorite{
          display: block;
          margin-bottom: 4px;
          color:#d4d6d9;
          line-height: 24px;
          font-size: 24px;
          &.active{
            color:rgb(240,20,20)
          }
        }
        .text{
          line-height: 10px;
          font-size: 10px;
          color:rgb(77,85,93)
        }
      }
    }
    .bulletin {
      padding: 18px 18px 0 18px;
      .title {
        margin: 8px 0;
        line-height: 14px;
        font-size: 14px;
        color: rgb(7, 17, 27);
      }
      .content-wrapper {
        padding: 0 12px 16px 12px;
        .border-1px(rgba(7, 17, 27, 0.1));
        .content {
          line-height: 24px;
          font-size: 12px;
          color: rgb(240, 20, 20)
        }
      }
      .supports {
        .support-item {
          padding: 16px 12px;
          font-size: 0;
          .border-1px(rgba(7, 17, 27, 0.1));
          &:last-child {
            .border-none();
          }
          .icon {
            display: inline-block;
            width: 16px;
            height: 16px;
            vertical-align: top;
            margin-right: 6px;
            background-size: 16px 16px;
            background-repeat: no-repeat;
            &.decrease {
              .bg-image('../../components/seller/decrease_4')
            }
            &.discount {
              .bg-image('../../components/seller/discount_4')
            }
            &.guarantee {
              .bg-image('../../components/seller/guarantee_4')
            }
            &.invoice {
              .bg-image('../../components/seller/invoice_4')
            }
            &.special {
              .bg-image('../../components/seller/special_4')
            }
          }
          .text {
            line-height: 16px;
            font-size: 12px;
            color: rgb(7, 17, 27);
          }
        }
      }
    }
    .pics{
      padding: 18px;
      .title{
        margin-bottom: 12px;
        line-height: 14px;
        font-size: 14px;
        color: rgb(7, 17, 27);
      }
      .pic-wrapper{
        width: 100%;
        overflow: hidden;
        white-space: nowrap;
        .pic-list{
          font-size: 0;
          .pic-item{
            display: inline-block;
            margin-right: 6px;
            width: 120px;
            height: 90px;
            &:last-child{
              margin-right: 0;
            }
          }
        }
      }
    }
    .info{
      padding: 18px 18px 0 18px;
      .title {
       padding-bottom: 12px;
        line-height: 14px;
        font-size: 14px;
        color: rgb(7, 17, 27);
        .border-1px(rgba(7,17,27,0.1));
      }
      .info-item{
        padding: 16px 12px;
        line-height: 16px;
        .border-1px(rgba(7,17,27,0.1));
        color: rgb(7, 17, 27);
        font-size: 12px;
        &:last-child{
          .border-none();
        }
      }
    }
  }
</style>



